.contact textarea {
    box-sizing: border-box;
    margin: 25px 0;
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    line-height: 1.6;
    padding: 10px;
}

.contact button {
    display: block;
    border: 2px solid black;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    margin: 0 20% 0 auto;
    transition: .3s;
}

.contact button:hover {
    background-color: #000;
    color: gold
}